<template>
	<view>
		<button open-type="share">小程序分享</button>
		<button @click="uniShare()">uniapp分享</button>
		<button type="primary" open-type="contact">查看微信客服</button>
	</view>
</template>

<script>
export default {
	data() {
		return {};
	},
	//小程序自带分享到好友
	// onShareAppMessage() {
	// 	return {
	// 		title: '小程序可主动分享到好友',
	// 		path: '/page/user?id=123', // 路径，传递参数到指定页面。
	// 		imgUrl: '' //自定义图片路径，可以是本地文件路径、代码包文件路径或者网络图片路径，支持PNG及JPG，不传入 imageUrl 则使用默认截图。显示图片长宽比是 5:4
	// 	};
	// },
	// //分享到好友圈
	// onShareTimeline: function() {
	// 	return {
	// 		title: '小程序右上角分享到好友圈',
	// 		path: '/page/user?id=123',
	// 		imgUrl: ''
	// 	};
	// },
	onLoad() {
		uni.showShareMenu({
			//设置下方的Menus菜单，才能够让发送给朋友与分享到朋友圈两个按钮可以点击
			menus: ['shareAppMessage', 'shareTimeline'],
			title: 'uni封装显示小程序默认分享菜单，用hideShareMenu()关闭',
			path: '/page/user?id=123',
			imgUrl: ''
		});
	},
	methods: {
		//分享到微信聊天界面，仅支持app，需要配置
		//https://uniapp.dcloud.net.cn/api/plugins/share.html
		uniShare() {
			uni.share({
				provider: 'weixin',
				scene: 'WXSceneSession', //scene: "WXSceneTimeline",时为分享到微信朋友圈
				type: 1, //0	图文  1	纯文字  2	纯图片  3	音乐  4	视频  5	小程序
				summary: '11111111111111111111！',
				success: function(res) {
					console.log('success:' + JSON.stringify(res));
				},
				fail: function(err) {
					console.log('fail:' + JSON.stringify(err));
				}
			});
		}
	}
};
</script>

<style lang="less">
button::after {
	border: none;
}
</style>
